.loader, .error {
  display: none;
}

img {
  width: 500px;
  display: none;
}

.breed-select {
  cursor: pointer;
  width: 30vw;
  color: #333;
  border: 1px solid #000;
  border-radius: 15px;
  margin-bottom: 30px;
  padding: 10px;
  font-size: 20px;
